// Header
.header-branding {
  position: relative;
  z-index: 1;
  color: black;
  background-color: @white;
  padding: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  height: 100%;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.1);
  @media screen and (max-width: @screen-sm) {
    text-align: center;
  }
  .container {
    display: flex;
    align-items: center;
    @media screen and (max-width: @screen-sm) {
      padding-left: @cont-padding;
      padding-right: @cont-padding;
      flex-direction: row;
    }
  }
  .brand {
    display: block;
    width: 194px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 1px;
    margin-top: 20px;
    margin-bottom: 73px;
    img {
      padding: 0.35rem 0;
      @media screen and (max-width: @screen-sm) {
        max-width: 9rem;
      }
    }
    @media screen and (max-width: @screen-sm) {
      margin-top: 0;
      margin-bottom: 0;
    }
  }
  .tagline {
    font-weight: 200;
    color: black;
    flex: 1 auto;
    text-align: right;
    padding: @space-xs 0;
    display: flex;
    position: relative;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: @screen-sm) {
      flex-direction: column;
      align-items: flex-end;
      text-align: right;
      display: none;
    }
  }
  a {
    color: white;
    cursor: pointer;
    font-weight: 400;
    transition: 250ms all ease;
    display: inline-block;
  }
  a:hover,
  a:active {
    opacity: .8;
    color: white;
    text-decoration: none;
    transition: 250ms all ease;
    opacity: 1;
  }
  .dropdown {
    padding: .2rem;
    border: 1px solid #E3E3E3;
    border-radius: 4px;
    margin-right: 16px;
  }
  @media screen and (max-width: @screen-sm) {
    .dropdown {
      padding: .2rem 0;
      white-space: nowrap;
      display: inline-block;
      font-size: 14px;
      .dropdown-menu {
        right: -10px;
        left: auto;
        min-width: auto;
        left: auto;
        top: 2.45rem;
        font-size: 14px;
        & > li > a {
          font-size: 14px;
          padding: @space-xs 30px @space-xs 15px;
          position: relative;
        }
      }
    }
  }
  .dropdown-toggle {
    font-size: 14px;
    padding: .5em .8rem;
  }
  .dropdown-gas {
    display: inline-block;
    @media screen and (max-width: @screen-sm) {
      margin-bottom: 1.8rem;
    }
    .dropdown-toggle {
      min-width: 200px;
    }
    .dropdown-menu{
      min-width: 100%;
      padding: .5rem;
      margin: 0;
      left: 0;
      right: 0;
      p {
        padding: 0;
      }
    }
  }
  .dropdown-menu--to-up {
    left: 0;
    top: 0;
    transform: translateY(-100%);
    width: 100%;
  }
}

.dropdown-menu a.active {
  text-decoration: none;
  color: @brand-primary;
  background-color: @gray-lightest;
}



// Navigation
.nav-container {
  position: fixed;
  height: 100vh;
  width: 340px;
  left: 0;
  top: 0;
  background-color: #3C3636;
  z-index: 6;
  overflow-y: auto;

  @media screen and (max-width: @screen-sm) {
    width: 100%;
    height: 60px;
    padding-top: 7px;
    padding-bottom: 0;
    overflow-y: visible;
  }

  .nav-scroll {
    -ms-overflow-style: -ms-autohiding-scrollbar;
    -webkit-overflow-scrolling: touch;
    overflow-x: auto;
    margin-bottom: -20px;
    font-size: 0;
    white-space: nowrap;
    display: block;
    @media screen and (max-width: @screen-sm) {
      margin: 0 -5% -20px;
      background: @ether-dark;
      display: none;
    }

    .nav-inner {
      display: inline-block;
      font-size: 0;
      margin-bottom: 20px;
      min-width: 100%;
      padding: 5px 0 0;
      vertical-align: middle;
      @media screen and (max-width: @screen-sm) {
        padding-top: 55px;
      }
      .nav-item {
