          background-position: center;
          background-repeat: no-repeat;
          background-size: contain;
          vertical-align: middle;
          margin-right: @space-xs;
        }
        @media screen and (max-width: @screen-sm) {
          width: 260px;
          margin-left: auto;
          margin-right: auto;
        }
        a {
          color: darken(@link-color-white, 15%);
          display: block;
          font-size: 18px;
          font-weight: 400;
          padding: 20px;
          padding-left: 60px;
          white-space: nowrap;
          position: relative;
          min-height: 2.75rem;
          @media screen and (max-width: @screen-sm) {
            padding: 10px;
            padding-left: 20px;
          }
        }
        a:after {
          content: "";
          background: @brand-primary;
          height: 100%;
          width: 4px;
          left: 0px;
          position: absolute;
          bottom: 0;
          transition: all 250ms ease 0s;
          transform: scale(0);
        }
        &.active a,
        a:hover,
        a:focus {
          text-decoration: none;
          transition: all 250ms ease 0s;
          @media screen and (max-width: @screen-sm) {
            background-color: @brand-primary;
            color: @black;
          }
          &:after {
            transform: scale(1);
            transition: all 250ms ease 0s;
          }
        }
        a:hover {
          
        }
      }
    }
  }

  .nav-scroll--mobile.visible {
    display: block !important;
  }
  .nav-scroll--mobile {
    @media screen and (min-width: @screen-sm) {
      display: none;
    }
  }

  .nav-arrow-right,
  .nav-arrow-left {
    background-color: white;
    bottom: 12px;
    color: #d6d6d6;
    font-size: 33px;
    line-height: 1.3;
    min-width: 50px;
    position: absolute;
    top: 0;
    vertical-align: middle;
    width: 5%;
    z-index: 999;
    &:hover {
      text-decoration: none;
    }
  }
  .nav-arrow-right {
    right: 3%;
    background: linear-gradient(to right, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 47%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    padding-right: 5px;
    text-align: right;
    @media screen and (max-width: @screen-sm) {
      right: 0;
    }
  }
  .nav-arrow-left {
    left: 3%;
    background: linear-gradient(to left, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 47%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    padding-left: 5px;
    text-align: left;
    @media screen and (max-width: @screen-sm) {
      left: 0;
    }
  }

  .dropdown {
    border: none;
    margin: 0;
    padding: 0;
    border-radius: 0;
    position: fixed;
    bottom: 0;
    height: 70px;
    width: 340px;
    box-sizing: border-box;
    @media screen and (max-width: @screen-sm) {
      display: none;
    }
  }
  a.btn-white {
    display: block;
    border-radius: 0;
    background-color: @ether-darken;
    color: @white;
    text-align: left;
    border: none;
    border-top: 1px solid @table-border-color;
    padding-top: 26px;
    padding-bottom: 26px;
    padding-left: 40px;
    font-size: @font-size-bump;
    @media screen and (max-width: @screen-sm) {
      padding-top: 28px;
      padding-bottom: 18px;
      display: none;
    }
  }
  .nav-toggler {
    position: absolute;
    top: 23px;
    left: 23px;
    display: none;
    width: 18px;
    height: 16px;
    flex-direction: column;
    justify-content: space-around;
    background-color: transparent;
    border: none;
    outline: none;
    @media screen and (max-width: @screen-sm) {
      display: flex;
    }
    span {
      display: block;
      background: @white;
      height: 2px;
      width: 18px;
    }
  }
  .nav-settings {
    position: absolute;
    top: 15px;
    right: 17px;
    z-index: 100;
    display: none;
    border: none;
    width: 30px;
    height: 30px;
    background: url(../images/settings.svg) center center no-repeat;
    @media screen and (max-width: @screen-sm) {
      display: inline-block;
    }
  }
}
